.list {
    display: flex;
    justify-content: center;
    margin: 10px;
  }
  .button {
    display: inline-block;
    text-transform: capitalize;
    margin-right: 10px;
    padding: 10px 15px;
    font-size: 15px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: #089A9A;
    border: none;
    border-radius: 5px;
    
  }
  .button:hover {
    box-shadow: 0 5px 3px #234C4B;
  }
  
  .button:active {
    background-color: #132CEA;
    transform: translateY(4px);
  }